    /**
     *  - tab_card
     */
    var changeTabButtonStatesFn = function () {
        var wareStatus = $('[name="wareStatus"] option:selected').attr('data-ware-status');
        var parent = $('#b_product_state_tab');
        var batchBtn = parent.find('.ingress_goods_btn');
        var importBtn = parent.find('.import_goods_btn');
        var delBtn = parent.find('.item_del_btn');
        switch (Number(wareStatus)) {
            case 1:
                //
                batchBtn.hide();
                importBtn.hide();
                delBtn.show();
                break;
            case 2:
                //
                batchBtn.show();
                batchBtn.removeClass('btngray').addClass('btnblue').html('').attr('id', 'batchIngressBtn').data('status', '3');
                importBtn.html('').attr('id', 'batchImportDownBtn').data('status', '3').show();
                delBtn.hide();
                break;
            case 3:
                //
                batchBtn.show();
                batchBtn.removeClass('btngray').addClass('btnblue').html('').attr('id', 'batchIngressBtn').data('status', '2');
                importBtn.html('').attr('id', 'batchImportUpBtn').data('status', '2').show();
                delBtn.show();
                break;
            case 8:
                //
                batchBtn.hide();
                importBtn.hide();
                delBtn.show();
                break;
            default:
                //
                batchBtn.show();
                batchBtn.removeClass('btnblue').addClass('btngray').html('/').attr('id', '');
                importBtn.hide();
                delBtn.show();
                break;
        }
    }



