    $('#importUpForm').submit(function () {
        callbacks.importCallback('', '');
        $('.popup .btnarea1').hide();
        //
        $(this).ajaxSubmit({
            type: 'post',
            url: urls.importUpShelfUrl,
            timeout: 30000,
            success: function (obj) {
                obj = $.parseJSON(obj);
                if (obj && obj.isSuccess) {
                    if (parseInt(obj.successNum) == parseInt(obj.totalNum)) {
                        $('.popup').find('.tips_red').html('').removeClass('tips_red').addClass('tips_blue');
                    } else {
                        var source = $('#template_item_import3').html();
                        var template = Handlebars.compile(source);
                        var html = template(obj);
                        $('.popup').html(html);
                        obj.repeatRows.map(function (item) {
                            $('.error_list').append('<li> ' + item + ' </li>');
                        });
                        obj.failRows.map(function (item) {
                            $('.error_list').append('<li> ' + item + ' </li>');
                        });
                        obj.parseFailRows.map(function (item) {
                            $('.error_list').append('<li> ' + item + ' </li>');
                        });
                        $('.popup .btnarea1').show();
                        $('.popup').show();
                    }
                } else {
                    $('.popup').find('.tips_red').html('');
                    $('.popup .btnarea1').show();
                }
            },
            error: function (err) {
                $('.popup').find('.tips_red').html('');
                $('.popup .btnarea1').show();
            }
        });
        return false; //
    });
